const PRECISION = 7;

function buildHeader() {
  return `#usda 1.0
(
    customLayerData = {
        string creator = "Three.js USDZExporter"
    }
    metersPerUnit = 1
    upAxis = "Y"
)

`;
}

function buildUSDFileAsString(dataToInsert) {
  let output = buildHeader();
  output += dataToInsert;
  return strToU8(output);
}

// Xform

function buildXform(object, geometry, material) {
  const name = 'Object_' + object.id;
  const transform = buildMatrix(object.matrixWorld);

  if (object.matrixWorld.determinant() < 0) {
    console.warn(
      'THREE.USDZExporter: USDZ does not support negative scales',
      object,
    );
  }

  return `def Xform "${name}" (
    prepend references = @./geometries/Geometry_${geometry.id}.usd@</Geometry>
)
{
    matrix4d xformOp:transform = ${transform}
    uniform token[] xformOpOrder = ["xformOp:transform"]

    rel material:binding = </Materials/Material_${material.id}>
}

`;
}

function buildMatrix(matrix) {
  const array = matrix.elements;

  return `( ${buildMatrixRow(array, 0)}, ${buildMatrixRow(
    array,
    4,
  )}, ${buildMatrixRow(array, 8)}, ${buildMatrixRow(array, 12)} )`;
}

function buildMatrixRow(array, offset) {
  return `(${array[offset + 0]}, ${array[offset + 1]}, ${array[offset + 2]}, ${
    array[offset + 3]
  })`;
}

// Mesh

function buildMeshObject(geometry) {
  const mesh = buildMesh(geometry);
  return `
def "Geometry"
{
  ${mesh}
}
`;
}

function buildMesh(geometry) {
  const name = 'Geometry';
  const attributes = geometry.attributes;
  const count = attributes.position.count;

  return `
    def Mesh "${name}"
    {
        int[] faceVertexCounts = [${buildMeshVertexCount(geometry)}]
        int[] faceVertexIndices = [${buildMeshVertexIndices(geometry)}]
        normal3f[] normals = [${buildVector3Array(attributes.normal, count)}] (
            interpolation = "vertex"
        )
        point3f[] points = [${buildVector3Array(attributes.position, count)}]
        float2[] primvars:st = [${buildVector2Array(attributes.uv, count)}] (
            interpolation = "vertex"
        )
        uniform token subdivisionScheme = "none"
    }
`;
}

function buildMeshVertexCount(geometry) {
  const count =
    geometry.index !== null
      ? geometry.index.array.length
      : geometry.attributes.position.count;

  return Array(count / 3)
    .fill(3)
    .join(', ');
}

function buildMeshVertexIndices(geometry) {
  if (geometry.index !== null) {
    return geometry.index.array.join(', ');
  }

  const array = [];
  const length = geometry.attributes.position.count;

  for (let i = 0; i < length; i++) {
    array.push(i);
  }

  return array.join(', ');
}

function buildVector3Array(attribute, count) {
  if (attribute === undefined) {
    console.warn('USDZExporter: Normals missing.');
    return Array(count).fill('(0, 0, 0)').join(', ');
  }

  const array = [];
  const data = attribute.array;

  for (let i = 0; i < data.length; i += 3) {
    array.push(
      `(${data[i + 0].toPrecision(PRECISION)}, ${data[i + 1].toPrecision(
        PRECISION,
      )}, ${data[i + 2].toPrecision(PRECISION)})`,
    );
  }

  return array.join(', ');
}

function buildVector2Array(attribute, count) {
  if (attribute === undefined) {
    console.warn('USDZExporter: UVs missing.');
    return Array(count).fill('(0, 0)').join(', ');
  }

  const array = [];
  const data = attribute.array;

  for (let i = 0; i < data.length; i += 2) {
    array.push(
      `(${data[i + 0].toPrecision(PRECISION)}, ${
        1 - data[i + 1].toPrecision(PRECISION)
      })`,
    );
  }

  return array.join(', ');
}

